import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: '#2ECC83',
    width:'100%',
    height:'80px',
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    marginTop: 'auto',
    marginBottom: 0,
  },
  foot_title: {
    color: 'white',
  }
}));

export default function SpacingGrid() {
  
  const classes = useStyles();

  

  return (
    <div className={classes.root}>
      <Typography variant="h6" gutterBottom className={classes.foot_title}>
        Ascii Art Generator  2021
      </Typography>
      <Typography variant="body2" gutterBottom className={classes.foot_title}>
        Contact us: hsnuwindband52@gmail.com 
      </Typography>
    </div>
  );
}