import React from 'react';
import { makeStyles,  withStyles} from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import {Link} from 'react-router-dom';
import { useHistory } from "react-router-dom";

import AuthContext from "./AuthContext.jsx";
import {logout} from '../api/ascii-art-generator-api.js'

const useStyles = makeStyles((theme) => ({
  root: {
    margin: theme.spacing(1),
  },
  barButton: {
    margin: theme.spacing(1),
    //border: '0px solid white',
  },
  registerButton: {
    margin: theme.spacing(1),
    
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
  },
  large: {
    width: theme.spacing(7),
    height: theme.spacing(7),
  },
  iconButton: {
    padding: 3,
    //border: '3px solid white',
    position: "relative",
    "&:hover": {
      backgroundColor: "#5DF5AF" 
    }
  },
}));

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.common.white,
    backgroundColor: "#29D787",
    '&:hover': {
      backgroundColor: '#23EA8E',
    },
  },
}))(Button);

export default function LoginIcon(props){
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const {user, setUser, isLoadingUser, setIsLoadingUser} = React.useContext(AuthContext);
  const history = useHistory();

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () =>{
    setIsLoadingUser(false);
    // clear user data
    setUser({});
    logout();
    history.push("/");
    setAnchorEl(null);
  } 
  
  return(
    user.access_token ?
    (<Grid >
      <IconButton color="inherit" className={classes.iconButton} onClick={handleClick}>
        <Avatar >{user.user_name}</Avatar>
      </IconButton>
      <Menu
        id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose}>Profile</MenuItem>
        <MenuItem onClick={handleClose}>My account</MenuItem>
        <MenuItem onClick={handleLogout}>Logout</MenuItem>
      </Menu>
    </Grid>)
    : 
    isLoadingUser ? null :
    (<Grid >
      <ColorButton 
        variant="contained" 
        size="small" 
        color="primary" 
        className={classes.registerButton}
        value={'/register'} 
        component={Link} 
        to={'/register'}
      >
        Register
      </ColorButton>
      <Button 
        variant="outlined" 
        size="small" 
        color="inherit" 
        className={classes.barButton}
        value={'/login'} 
        component={Link} 
        to={'/login'}
      >
        Login
      </Button>
    </Grid>)
  )
}