import React from 'react';

import Container from '@material-ui/core/Container';
import { makeStyles , withStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';

import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import { useHistory } from "react-router-dom";


import AuthContext from "../conponent/AuthContext.jsx";
import LoadingIcon from '../conponent/LoadingIcon';
import {generateAsciiArt} from '../api/ascii-art-generator-api.js';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    minWidth: theme.spacing(40),
    '& > *': {
      marginTop: "20%",
      marginLeft: 'auto',
      marginRight: 'auto',
      minWidth: theme.spacing(40),
      maxWidth: theme.spacing(40),
      //height: theme.spacing(42),
      borderRadius: theme.spacing(1),
    },
  },
  title: {
    marginTop: theme.spacing(1),
    marginLeft: 'auto',
    marginRight: 'auto',
    color:"#2ECC83",
  },
  form: {
    display: 'inline-block',
    
    '& .MuiTextField-root': {
      marginTop: theme.spacing(3),
      marginLeft: 'auto',
      marginRight: 'auto',
      width: 200,
    },
    '& .input-small': {
      margin:theme.spacing(2),
      width: 100,
      align: 'left',
    },
    '& .preview-div': {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(0),
      marginLeft: 'auto',
      marginRight: 'auto',
      
    },
  },
  iconButton: {
    padding: theme.spacing(0.5),
    marginTop: theme.spacing(2),
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    textAlign: 'center',
    border: '1px solid gray',
  },
  submitButton: {
    padding: 0,
    marginTop: theme.spacing(4),
    marginLeft: 'auto',
    marginRight: 'auto',
    marginBottom: theme.spacing(2),
    textAlign: 'center',
    width: 200,
    height: 30,
    //border: '1px solid white',
  },
  loadingDiv: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  textDiv:{
    minWidth: theme.spacing(40),
    maxWidth: theme.spacing(100),
    marginLeft: 'auto',
    marginRight: 'auto',
    display: 'flex',
    flexDirection: 'column',
    '& > *': {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(1),
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  textArea:{
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(2),
    overflow:'scroll',
    overflowY: 'scroll',
    overflowX: 'scroll',
  },
  textAreaButton:{
    width: theme.spacing(40),
    height: 30,
    padding: 0,
    marginLeft: 'auto',
    marginRight: 'auto',
    marginBottom: theme.spacing(2),
    textAlign: 'center',  
  },
}));
const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.common.white,
    backgroundColor: "#2ECC83",
    '&:hover': {
      backgroundColor: '#23EA8E',
    },
  },
}))(Button);

const ImageState = Object.freeze({"Unupload":1, "Waiting":2, "Completed":3});

export default function Image2AsciiartPage(props){
  const classes = useStyles();
  const {user, setUser} = React.useContext(AuthContext);
  const [asciiArt, setAsciiArt] = React.useState({string:"", width:30, height:0});
  const [uploadedFile, setUploadedFile] 
  = React.useState({
    image: undefined,
    previewImage: undefined,
    state: ImageState.Unupload,
  });
  const history = useHistory();

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if(!uploadedFile.image)
      return

    const img = document.querySelector(".preview-image");
    console.log("submit");
    console.log(`width : ${img.naturalWidth}, height : ${img.naturalHeight}`);

    setUploadedFile(uploadedFile => ({ 
      ...uploadedFile, 
      state: ImageState.Waiting,
    }));
    let res = await generateAsciiArt(uploadedFile.image, 11, asciiArt.width*30, 'cnn_0');
    //console.log(res)
    setUploadedFile(uploadedFile => ({ 
      ...uploadedFile, 
      state: ImageState.Completed,
    }));
    let asciiString = processAsciiArray(res.ascii_art);
    setAsciiArt({string: asciiString, width: res.ascii_art[0].length+10, height: res.ascii_art.length})
    localStorage.setItem("asciiart_string_tmp", asciiString);
  };

  const selectFile = async (e) => {
    setUploadedFile(uploadedFile => ({ 
      //...uploadedFile, 
      image: e.target.files[0], 
      previewImage: URL.createObjectURL(e.target.files[0]),
      state: ImageState.Unupload,
    }));
  };

  const handleAsciiWidth= async (e) => {
    setAsciiArt(ascii => ({ 
      ...ascii, 
      width: e.target.value,
    }));
  };

  const handleTextAreaChange = async (e) => {
    setAsciiArt(ascii => ({ 
      ...ascii, 
      string: e.target.value,
    }));
  };
  return(
  <div id = 'page' >
    <Container maxWidth={'sm'} direction="column">
        
      <div className={classes.root}>
        
        <Paper elevation={3}>
          <Typography variant="h6" align = 'center' className={classes.title} gutterBottom>
            Generate a new ascii art
          </Typography>

          <Divider  variant="middle"/>
          <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
          >
            <form className={classes.form} onSubmit={handleSubmit} align = 'center' noValidate autoComplete="off">
              
              <label htmlFor="btn-upload">
                <input
                  id="btn-upload"
                  name="btn-upload"
                  style={{ display: 'none' }}
                  type="file"
                  accept="image/*"
                  onChange={selectFile} />
                <Button
                  className={classes.iconButton}
                  variant="outlined"
                  component="span" >
                  Choose a Image
                </Button>
              </label>
            
              <div className="preview-div">
                {uploadedFile.previewImage && (
                  <img className="preview-image" src={uploadedFile.previewImage} width={135} alt={uploadedFile.image.name} />
                )}
              </div>
              <div className="file-name">
                {uploadedFile.image? uploadedFile.image.name : null}
              </div>

              <Grid
                container
                direction="row"
                justify="center"
                alignItems="flex-start"
              >
                <TextField
                  required
                  id="ascii-width"
                  label="Number of characters per line"
                  type="number"
                  defaultValue={30}
                  variant="outlined"
                  size="small"
                  onChange={handleAsciiWidth}
                />
              </Grid>

              <ColorButton type="submit" variant="contained"  color="primary" className={classes.submitButton}>
                Submit
              </ColorButton>
            </form>
          </Grid>
        </Paper>
        
      </div>
    </Container>
    
    <div align="center" className={classes.textDiv}>

      { uploadedFile.state === ImageState.Waiting ?  <LoadingIcon /> : null}
      { asciiArt.string!=="" ?
        [<ColorButton type="submit" variant="contained"  color="primary" className={classes.textAreaButton}>
          Create a Post
        </ColorButton>,
        <textarea 
          rows={asciiArt.height} 
          cols={asciiArt.width} 
          placeholder={""} 
          className={classes.textArea} 
          value = {asciiArt.string}
          onChange={handleTextAreaChange}
        /> ]
        :null
      }
    </div>

  </div>
  )
}
/**
 * send an image to server to generate an ascii art string.
 * 
 * @param {Array} asciiArray a 2D array contains asciiart characters.
 * 
 * @returns {string} return a string of ascii art
 */
function processAsciiArray(asciiArray){
  let output = [];
  for(let i = 0; i < asciiArray.length; i++){
    let str = asciiArray[i].join('');
    output.push(str);
    output.push('\n');
  }
  return output.join('');
}