import React from 'react';
import HomeIcon from '@material-ui/icons/Home';
import MailIcon from '@material-ui/icons/Mail';
import EqualizerIcon from '@material-ui/icons/Equalizer';

import CreatePostsPage from '../pages/CreatePostsPage.jsx';
import Image2AsciiartPage from '../pages/Image2AsciiartPage';
import HomePage from '../pages/HomePage';
const iconList = [<HomeIcon/>, <MailIcon/>, <EqualizerIcon/>];
export {iconList};

const routeLists = ['/', '/image2aa','/posts', '/statistics','/posts/create_posts','/posts/search_posts_bytags'];
export {routeLists};

const tabNames = ['Home', 'Posts', 'Statistics'];
export {tabNames};

const configTree = {
  childLists: [
    {
      name:'Home',
      route: '/',
      icon: <HomeIcon/>,
      renderObject: <HomePage/>,
      childLists:[]
    },
    {
      name:'Posts',
      route: '/posts',
      icon: <MailIcon/>,
      renderObject:null,
      childLists:[
        {
          name:'Create a post',
          route: '/posts/create_posts',
          icon: <MailIcon/>,
          renderObject: <CreatePostsPage/>,
          childLists:[]
        },
        {
          name:'Search by tags',
          route: '/posts/search_posts_bytags',
          icon: <MailIcon/>,
          renderObject:null,
          childLists:[]
        },
      ]
    },
    {
      name:'Image to AsciiArt',
      route: '/image2aa',
      icon: <EqualizerIcon/>,
      renderObject:<Image2AsciiartPage/>,
      childLists:[]
    },
    {
      name:'Statistics',
      route: '/statistics',
      icon: <EqualizerIcon/>,
      renderObject:null,
      childLists:[]
    }
  ]
}
export {configTree};

/**
 * Get routes from a nested array childLists 
 *
 * @returns {Array} a 1-dimension array contains routes and its React elements.
 */
export function getRoutesArray(){
  let storedArray = [];
  return getRoutesRecursively(configTree.childLists, storedArray);
}

/**
 * Get routes from a nested array childLists by recursion
 *
 * @param {Array} childLists an array contains route objects
 * @param {Array} storedArray an array contains temporary data
 * 
 * @returns {Array} a 1-dimension array contains routes and and its React elements.
 */
function getRoutesRecursively(childLists, storedArray){
  for(let i = 0; i < childLists.length; i++){
    storedArray.push({
      route: childLists[i].route,
      renderObject: childLists[i].renderObject,
    });
    if(childLists[i].childLists.length > 0)
      getRoutesRecursively(childLists[i].childLists, storedArray);
  }
  return storedArray;
}