/**
 * send a GET request with refresh token in cookies, to get a new access token.
 * @returns {JSON} http response
 */
export async function refreshAccessToken(){
  let url = `/accessToken`;
  console.log(`Making request to: ${url}`);

  const config = {
    withCredentials: true
  }
  
  try {
    return axiosClient.get(url, config);
  } catch(err) {
    console.log(`Error occurs during refreshing an access token :` + err);
    throw err;
  }
}

/**
 * send a DELETE request to clear refresh_token
 * @returns {JSON} http response
 */
 export async function logout(){
  let url = `/accessToken`;
  console.log(`Making request to: ${url}`);

  const config = {
    withCredentials: true
  }
  
  try {
    return axiosClient.delete(url, config);
  } catch(err) {
    console.log(`Error occurs during deleting the refresh token :` + err);
    throw err;
  }
}



/**
 * send a GET request to server. Let server to authenticate the user.
 * 
 * @param {string} access_token a JWT used to get the api authentication.
 * 
 * @returns {Object} If access_token is valid then return { status:"success", new_token: ""}.
 *                   If access_token is expired and refresh_token is valid then return
 *                   { status:"update", new_token: access_token}  
 *                   else, if access_token and refresh_token both are expired , then return 
 *                   { status:"fail", new_token: ""}
 */
export async function authenticate(access_token){
  let url = `/accessTokenAuth`;
  console.log(`Making request to: ${url} to get authenticated.`);

  const config = {
    headers: {
      'authorization': `Bearer ${access_token}`,
    },
  }
  try {
    // send a GET request to determine user's status.
    var user_res = await axiosClient.get(url,config);
  } catch(err) {
    console.log(`Error occurs during authenticating token :` + err);
    throw err;
  }

  if(user_res.data.success)
    return { status:"success", new_token: ""};
  
  // if there is no access_token or access_token is expired.
  // try to refresh an access_token.
  let refresh_res = await refreshAccessToken();

  if(refresh_res.data.success)
    return { status:"update", new_token: refresh_res.data.access_token} ;
  
  // if access_token and refresh_token are both expired
  return { status:"fail", new_token: ""};
}

/**
 * Get user's profile
 * 
 * @param {string} access_token a JWT used to get the api authentication.
 * 
 * @returns {JSON} http response
 */
export async function getUserProfile(access_token) {
  let url = `/user`;
  console.log(`Making request to: ${url}`);

  const config = {
    headers: {
      'authorization': `Bearer ${access_token}`,
    },
    withCredentials: true
  }
  try {
    let res = await axiosClient.get(url,config);
    return res.data;
  } catch(err) {
    console.log(`Error occurs during get user's profile :` + err);
    throw err;
  }
}

/**
 * send an image to server to generate an ascii art string.
 * 
 * @param {string} access_token a JWT used to get the api authentication.
 * 
 * @returns {JSON} http response
 */
 export async function generateAsciiArt(image, kernal_zise, image_width, model_name) {
  let url = `/asciiArt`;
  console.log(`Making Post request to: ${url}`);

  const aaFormData = new FormData();
  aaFormData.append('image', image);
  aaFormData.append('kernal_zise', kernal_zise);
  aaFormData.append('image_width', image_width);
  aaFormData.append('model_name',  model_name);

  const config = {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    withCredentials: true
  }
  

  try {
    let res = await axiosClient.post(url, aaFormData, config);
    return res.data;
  } catch(err) {
    console.log(`Error occurs during get user's profile :` + err);
    throw err;
  }
}
