/**
 * search posts by keyword
 * 
 * @param {string} keyword s keyword string
 * @param {number} start the index that query starts from.
 * @returns {JSON} http response
 */
 export async function searchByKeyword( keyword = "", start = null) {
  let url = `/posts?search_words=${keyword}`;
  if(keyword === "")
    url = `/posts`;
  
  if(start)
    url = url + `?start=${start}`;
  console.log(`Making request to: ${url}`);

  
  try {
    let res = await axiosClient.get(url);
    return res.data;
  } catch(err) {
    console.log(`Error occurs during get user's profile :` + err);
    throw err;
  }
}

/**
 * Create a new post
 * 
 * @param {string} access_token a JWT used to get the api authentication.
 * @param {string} title the post title
 * @param {string} context ascii art string
 * @param {string} tags a string contains tags. eg: #tag1#tag2
 * @param {number} width width of ascii art
 * @param {number} height height of ascii art
 * 
 * @returns {JSON} http response
 */
 export async function createPost( access_token, title, context , tags, width, height, image=null ) {
  let url = `/posts`;
  console.log(`Making Post request to: ${url}`);

  const formData = new FormData();
  
  if(image)
    formData.append('image', image);
  formData.append('title',  title);
  formData.append('context',  context);
  formData.append('tags',  tags);
  formData.append('width',  width);
  formData.append('height',  height);

  const config = {
    headers: {
      'Content-Type': 'multipart/form-data',
      'authorization': `Bearer ${access_token}`,
    },
  }
  

  try {
    let res = await axiosClient.post(url, formData, config);
    return res.data;
  } catch(err) {
    console.log(`Error occurs during creating the new post :` + err);
    throw err;
  }
}

