import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { makeStyles} from '@material-ui/core/styles';
import {Link} from 'react-router-dom';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Divider from '@material-ui/core/Divider';

const useStyles = makeStyles((theme) => ({
  nested_0:{
    paddingLeft: theme.spacing(2),
  },
  nested_1:{
    paddingLeft: theme.spacing(4),
  },
  nested_2:{
    paddingLeft: theme.spacing(8),
  }
}));


export default function ListsRecursion(props){
  
  return props.childLists.map((listObject, index) => (
    <ExpandableList
      key = {index}
      listObject = {listObject}
      selectedListName = {props.selectedListName}
      setSelectedListName = {props.setSelectedListName}
      setBarName = {props.setBarName}
      level = {props.level}
      mainName = {props.mainName}
    />
  ))
}

function ExpandableList(props){
  const classes = useStyles();
  const [listOpen, setListOpen] = React.useState(false);

  const handleListOpen = () => {
    setListOpen(!listOpen);
  };
  const handleListSelect = (listName) => {
    props.setSelectedListName(listName);
  };
  
  return (
    <div >

    <ListItem button 
      className={classes[`nested_${props.level}`]}
      key={props.listObject.name} 
      value={props.listObject.route} 
      component={Link} 
      to={props.listObject.route}
      selected={props.selectedListName === props.listObject.name}
      onClick={() => { 
        props.setBarName(props.listObject.name === "Home" ? props.mainName : props.listObject.name); 
        handleListSelect(props.listObject.name);
        if(props.listObject.childLists.length > 0){
          handleListOpen();
        }
          
      }}
    >
      <ListItemIcon>{props.listObject.icon}</ListItemIcon>
      <ListItemText primary={props.listObject.name} />
      {
        props.listObject.childLists.length > 0 ?
          listOpen ?
            <ExpandLess /> : <ExpandMore />
        : null
      }
    </ListItem>
    {
      props.listObject.childLists.length > 0 ?
        <Collapse in={listOpen} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <Divider />
          
          <ListsRecursion
            childLists = {props.listObject.childLists} 
            level={props.level+1}
            setSelectedListName = {props.setSelectedListName}
            selectedListName = {props.selectedListName}
            setBarName = {props.setBarName}
            mainName = {props.mainName}
          />
          <Divider />
        </List>
        </Collapse>
      : null
    }
    </div>
  );
  
  
}