import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import { makeStyles , withStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import Avatar from "@material-ui/core/Avatar";
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import favicon from "../image/favicon_io/favicon.ico";
import Grid from '@material-ui/core/Grid';
import { useHistory } from "react-router-dom";

import {login, getUserProfile, } from '../api/ascii-art-generator-api.js'
import AuthContext from "../conponent/AuthContext.jsx";

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    //flexWrap: 'wrap',
    
    '& > *': {
      marginTop: "20%",
      marginLeft: 'auto',
      marginRight: 'auto',
      width: theme.spacing(40),
      height: theme.spacing(42),
      borderRadius: theme.spacing(1),
    },
  },
  title: {
    marginTop: theme.spacing(1),
    marginLeft: 'auto',
    marginRight: 'auto',
    color:"#2ECC83",
  },
  form: {
    display: 'inline-block',
    '& .MuiTextField-root': {
      marginTop: theme.spacing(3),
      marginLeft: 'auto',
      marginRight: 'auto',
      width: 200,
    },
    '& .input-label': {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(0),
      marginLeft: 'auto',
      marginRight: 'auto',
      width: 200,
      align: 'left',
    },
  },
  iconButton: {
    padding: 0,
    marginTop: theme.spacing(2),
    marginLeft: 'auto',
    marginRight: 'auto',
    textAlign: 'center',
    border: '1px solid gray',
  },
  submitButton: {
    padding: 0,
    marginTop: theme.spacing(4),
    marginLeft: 'auto',
    marginRight: 'auto',
    textAlign: 'center',
    width: 200,
    height: 30,
    //border: '1px solid white',
  },
}));
const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.common.white,
    backgroundColor: "#2ECC83",
    '&:hover': {
      backgroundColor: '#23EA8E',
    },
  },
}))(Button);
export default function LoginPage(props){
  const classes = useStyles();

  const [username, setUsername] = React.useState("");
  const [password, setPassword] = React.useState("");
  const {user, setUser} = React.useContext(AuthContext);
  const history = useHistory();
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    if(username === "" || password === "")
      return console.log(`username and password are required.`);
    
    console.log(`Username is ${username}`);
    let res = await login(username, password);

    if(!res.data.success)
      return console.log(`username or password is incorrect.`);
    
    setUser( res.data.result);
    // redirect to /
    history.push("/");
  };

  

  const getUser = async (e) => {
    if(!user.access_token)
      return console.log('Please log in first.')
    
    let res = await getUserProfile(user.access_token);
    console.log(res);
  };

  const handleUsername = (e) => {
    setUsername(e.target.value);
  };

  const handlePassword = (e) => {
    setPassword(e.target.value);
  };
  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="sm" direction="column">
        
        <div className={classes.root}>
        
          <Paper elevation={3}>
            <Typography variant="h6" align = 'center' className={classes.title} gutterBottom>
              LOG IN
            </Typography>

            <Divider  variant="middle"/>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
            >
              
              <IconButton color="inherit" className={classes.iconButton}>
                <Avatar src={favicon } />
              </IconButton>
              <form className={classes.form} onSubmit={handleSubmit} align = 'center' noValidate autoComplete="off">
              
                <TextField
                  required
                  id="username"
                  label="Username"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  onChange={handleUsername}
                />
              
                <TextField
                  required
                  id="password"
                  label="Password"
                  type="password"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  onChange={handlePassword}
                />
                
                <ColorButton type="submit" variant="contained"  color="primary" className={classes.submitButton}>
                  Submit
                </ColorButton>
              </form>
              
            </Grid>
          </Paper>
        </div>
      </Container>
    </React.Fragment>
  );
}