import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
} from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import AppBar from './conponent/AppBar';
import {iconList, routeLists, tabNames, configTree, getRoutesArray} from './conponent/AppBarConfig';
import LoginPage from './pages/LoginPage';
import RegisterPage from './pages/RegisterPage';
import Footer from './conponent/Footer';

import AuthContext from "./conponent/AuthContext.jsx";
import { refreshAccessToken, getUserProfile} from './api/ascii-art-generator-api.js'

function App() {
  const [user, setUser] = React.useState({access_token:""});
  // when component is initially rendered, isLoadingUser is true.
  const [isLoadingUser, setIsLoadingUser] = React.useState(true);

  async function checkRefreshToken(){
    // check if refresh token is alive.
    let ref_res = await refreshAccessToken();
    // if refresh token is alive.
    if(ref_res.data.success){
      let profile_res = await getUserProfile(ref_res.data.access_token);
      setIsLoadingUser(false);
      return setUser({
        access_token: ref_res.data.access_token,
        ...profile_res.result
      });
    }
    // if refreshAccessToken() fail, user need to login again.
    setIsLoadingUser(false);
    return console.log('Please log in first.');
  }

  // After component is initially rendered, check refresh token one time.
  React.useEffect(() => {
    checkRefreshToken();
  },[]);

  React.useEffect(() => {
    const timeout = setTimeout(async() => {
      checkRefreshToken();
    }, 300*1000);
  },[user.access_token]);

  let PathRoutes 
  = (getRoutesArray()).map(
    (routeObject) => 
      <Route key={routeObject.route} exact path={routeObject.route} render={() => (
        <div>  
          {routeObject.renderObject}
        </div>
      )}/>
  );

  return (
    <AuthContext.Provider value={{user, setUser, isLoadingUser, setIsLoadingUser}}>
      <CssBaseline />
      <Router>
        <Grid
          container
          direction="column"
          justify="center"
          alignItems="center"
          
        >
          <AppBar barName={'Ascii Art Generator'} configTree = {configTree} routeList={routeLists}  iconList={iconList} tabNames={tabNames}/>
          <div className="App">
          </div>
        </Grid>

        <div style = {{ 'minHeight': '100vh' , display:'flex', flexDirection:"column"}}>
          <Route key={'/login'} exact path={'/login'} render={() => (
            <LoginPage/>
          )}/>
          <Route key={'/register'} exact path={'/register'} render={() => (
            <RegisterPage/>
          )}/>
          {PathRoutes}
          <Footer/>
        </div>
        
        
      </Router>
    </AuthContext.Provider>
  );
}

export default App;
