import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import IconButton from '@material-ui/core/IconButton';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

import SearchInput from '../conponent/SearchInput';
import AsciiCard from '../conponent/AsciiCard';
import AsciiCardColumn from '../conponent/AsciiCardColumn';
import TwoLabelSwitch from '../conponent/TwoLabelSwitch';
import {searchByKeyword,  addPostLike} from '../api/ascii-art-generator-api';
import AuthContext from "../conponent/AuthContext.jsx";
import './HomePage.css'
const useStyles = makeStyles((theme) => ({
  searchBar: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    width:'100%',
    marginTop: "4em",
    '& > *': {
      //marginTop: "4em",
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
  },
  cardAreaRowType: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    width:'100%',
    alignItems: 'center',
    justifyContent: 'space-evenly',
    padding:'1em',
    '& > *': {
      marginTop: "1em",
    },
  },
  cardAreaColumnType: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'flex-start',
    
    '@media (max-width: 480px)':{

    },
    '@media (min-width: 768px) and (max-width: 979px)':{
      height:'4000px'
    },
    '@media (min-width: 980px)': {
      height:'3000px'
    },
    '@media (min-width: 1200px)': {
      height:'2000px'
    },
    padding:'1em',
    '& > *': {
      marginTop: "1em",
    },
  },
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    width:'100%',
    '& > .page-icon': {
      marginLeft: 'auto',
      marginRight: 'auto',
      marginBottom: theme.spacing(2),
      
    },
  },
}));



export default function HomePage() {
  const classes = useStyles();
  const [input, setInput] = React.useState("");
  const [posts, setPosts] = React.useState([]);
  const [postQuery, setPostQuery] = React.useState({keword: '', pageIndexArray: [], curPage: 0});
  const {user} = React.useContext(AuthContext);
  const [displayMode, setDisplayMode] = React.useState("column");
  const handleInputChange = async (e) => {
    setInput(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log('search submit');
    let res = await searchByKeyword(input);
    setPosts(res.result);

    if(res.result.length===0){ 
      setPostQuery({
        keword: input,
        pageIndexArray: [],
        curPage: 0,
      })
      return;
    }
  
    setPostQuery({
      keword: input,
      pageIndexArray: [res.result[0].id+1, res.last_id],
      curPage: 0,
    })
    console.log(res);
  }

  const handlePostLike = async (e, postId, cardId) => {
    e.preventDefault();
    if(!user.access_token)
      return console.log('Please log in first.');
    
    let res = await addPostLike(user.access_token, postId);

    if(res.data.success){
      posts[cardId].like_num = res.data.result.like_num;
      setPosts(prev =>( [...prev]));
    }
  }
  
  const goPrePage = async (e) => {
    if(postQuery.curPage === 0)
      return;

    let res = await searchByKeyword(postQuery.keword, postQuery.pageIndexArray[postQuery.curPage-1]);

    if(res.result.length===0)
      return;

    setPosts(res.result);
    setPostQuery(prev => ({
      ...prev,
      curPage: prev.curPage>0 ? prev.curPage-1 : prev.curPage,
    }))
  };

  const goNextPage = async (e) => {
    //if user is at the last page.
    if(postQuery.curPage === postQuery.pageIndexArray.length -1)
      return;

    let res = await searchByKeyword(postQuery.keword, postQuery.pageIndexArray[postQuery.curPage+1]);

    if(res.result.length===0)
      return;
    
    console.log(postQuery);
    setPosts(res.result);

    //if user has reached this page before.
    if(postQuery.curPage < postQuery.pageIndexArray.length-1){
      setPostQuery(prev => ({
        ...prev,
        curPage: prev.curPage+1,
      }))
      return;
    }
      
    setPostQuery(prev => ({
      keword: prev.keword,
      pageIndexArray: [...prev.pageIndexArray, res.last_id ],
      curPage: prev.curPage+1,
    }))
  };
  const changeDisplayMode = () => {
    switch (displayMode) {
      case 'column':
        setDisplayMode('row');
        break;
      case 'row':
        setDisplayMode('column');
        break;
      default:
        setDisplayMode('column');
    }    
  }
  // execute once at first rendering
  React.useEffect(() => {
    async function loadLatestPosts(){
      let res = await searchByKeyword("");
      if(res.success === true){
        setPosts(res.result);
        setPostQuery(prev => ({
            keword: prev.keword,
            pageIndexArray: [res.result[0].id+1, res.last_id] ,
            curPage: 0,
          })
        )
      }
    }
    loadLatestPosts();
  },[]);

  return (
    <div className={classes.root}>
      <div className={classes.searchBar}>
        <SearchInput onChange={handleInputChange} handleSubmit={handleSubmit}/>
        <TwoLabelSwitch callback={changeDisplayMode }/>
      </div>
      {displayMode==='column'?
        <div className={classes.cardAreaColumnType}>
          { posts.length ? generatePostsColumn(posts):null  }
        </div>
        :
        <div className={classes.cardAreaRowType}>
          { posts.length ? generatePosts(posts):null  }
        </div>

      }
      <div className='page-icon'>
        <IconButton onClick={goPrePage}>
          <NavigateBeforeIcon />
        </IconButton>
        <IconButton onClick={goNextPage}>
          <NavigateNextIcon />
        </IconButton>
      </div>
      
    </div>
  );

  function generatePosts(arr){
    let output = [];
    for(let i=0 ; i < arr.length; i++)
      output.push(
        <AsciiCard 
        context={arr[i].context} 
        contentHeight={400} 
        asciiHeight={arr[i].height} 
        asciiWidth={arr[i].width} 
        key={i}
        cardId={i}
        title={arr[i].title}
        like_num={arr[i].like_num}
        views={arr[i].views}
        postId={arr[i].id}
        userName={arr[i].user_name}
        handlePostLike={handlePostLike}
        user={user}
        
        />
      );
    return output;
  }

  function generatePostsColumn(arr){
    let output = [];
    for(let i=0 ; i < arr.length; i++)
      output.push(
        <AsciiCardColumn 
        context={arr[i].context} 
        contentHeight={400} 
        contentWidth={300}
        asciiHeight={arr[i].height} 
        asciiWidth={arr[i].width} 
        key={i}
        cardId={i}
        title={arr[i].title}
        like_num={arr[i].like_num}
        views={arr[i].views}
        postId={arr[i].id}
        userName={arr[i].user_name}
        handlePostLike={handlePostLike}
        user={user}
        />
      );
    return output;
  }
}

