import React from 'react';

import Container from '@material-ui/core/Container';
import { makeStyles , withStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import Avatar from "@material-ui/core/Avatar";
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import favicon from "../image/favicon_io/favicon.ico";
import Grid from '@material-ui/core/Grid';
import { useHistory } from "react-router-dom";

import AuthContext from "../conponent/AuthContext.jsx";
import {createPost} from '../api/ascii-art-generator-api.js'
const useStyles = makeStyles((theme) => ({
  
  root: {
    
    width:'90%',
    marginLeft: 'auto',
    marginRight: 'auto',
    padding:'1em',
    '& > *': {
      
      marginTop: "10%",
      
      marginLeft: 'auto',
      marginRight: 'auto',
      width:'100%',
      
      //height: theme.spacing(80),
      borderRadius: theme.spacing(1),
    },
  },
  title: {
    
    marginLeft: 'auto',
    marginRight: 'auto',
    color:"#2ECC83",
    paddingTop: theme.spacing(2),
  },
  textArea:{
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(2),
    width:'90%',
    whiteSpace:'nowrap',
    overflow:'scroll',
    overflowY: 'scroll',
    overflowX: 'scroll',
  },
  submitButton: {
    padding: 0,
    marginTop: theme.spacing(1),
    marginLeft: 'auto',
    marginRight: 'auto',
    marginBottom: theme.spacing(1),
    textAlign: 'center',
    width: '90%',
    height: 30,
    //border: '1px solid white',
  },
  input_title: {
    width:'90%',
    marginTop: theme.spacing(2),
  },
  input_tags: {
    width:'50%',
    marginTop: theme.spacing(2),
  },
  input_area: {
    width:'100%',
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    padding: '5%',
  },
}));
const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.common.white,
    backgroundColor: "#2ECC83",
    '&:hover': {
      backgroundColor: '#23EA8E',
    },
  },
}))(Button);
export default function CreatePostsPage(props){
  const classes = useStyles();
  const {user, setUser} = React.useContext(AuthContext);
  const history = useHistory();

  const asciiStr = localStorage.getItem('asciiart_string_tmp') ? localStorage.getItem('asciiart_string_tmp') : '';
  
  const [asciiArt, setAsciiArt] = 
  React.useState({
    string: asciiStr,
    width: 100, 
    height: 50,
    title: "",
    tags: "",
  });

  const handleTextAreaChange = async (e) => {
    setAsciiArt(ascii => ({ 
      ...ascii, 
      string: e.target.value,
    }));
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    if(!user.access_token)
      return console.log('Please log in first.');

    let newlineIndexArray = indexOfAll(asciiArt.string, '\n');
    let asciiWidth = findMaxDifference(newlineIndexArray) ;
    let asciiHeight = newlineIndexArray.length + 1;
    console.log(`( width : ${asciiWidth}, height : ${asciiHeight} )`);
    console.log(`title : ${asciiArt.title}, tags : ${asciiArt.tags} `);

    let res = await createPost( 
      user.access_token, 
      asciiArt.title, 
      asciiArt.string , 
      asciiArt.tags,
      asciiWidth,
      asciiHeight
    );
    localStorage.removeItem('asciiart_string_tmp');
    console.log(res);
    // redirect to /
    history.push("/");
  }
  const handleTitle = async (e) => {
    e.preventDefault();
    setAsciiArt(ascii => ({ 
      ...ascii, 
      title: e.target.value,
    }));
  }
  const handleTags = async (e) => {
    e.preventDefault();
    const tags = e.target.value.replaceAll(' ', '');
    setAsciiArt(ascii => ({ 
      ...ascii, 
      tags: tags,
    }));
    
  }
  return(
    
        
      <div className={classes.root}>
        
        <Paper elevation={4}>
          <Typography variant="h4" align = 'center' className={classes.title} >
            Create a new post
          </Typography>

          <Divider  variant="middle"/>
          <form className={classes.form} onSubmit={handleSubmit} align = 'center' noValidate autoComplete="off">
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
            >
              <div className={classes.input_area}>
                <TextField
                  required
                  id="post-title"
                  label="Title"
                  type="text"
                  defaultValue={""}
                  variant="outlined"
                  size="small"
                  className={classes.input_title}
                  onChange={handleTitle}
                />
                <TextField
                  required
                  id="post-tags"
                  label="Tags : eg. #tag1#tag2..."
                  type="text"
                  defaultValue={""}
                  variant="outlined"
                  size="small"
                  className={classes.input_tags}
                  onChange={handleTags}
                />
              </div>
              
              <textarea 
                rows={asciiArt.height} 
                cols={asciiArt.width} 
                placeholder={""} 
                className={classes.textArea} 
                value = {asciiArt.string}
                onChange={handleTextAreaChange}
              /> 
              <ColorButton type="submit" variant="contained"  color="primary" className={classes.submitButton}>
                Create a Post
              </ColorButton>,
            </Grid>
          </form>
        </Paper>
      </div>
    
  )
}

/**
 * get all substring's indexes.
 * 
 * @param {string} str the string which you want search substring in.
 * @param {string} substring the substring you want to find its index in string.
 * 
 * @returns {array} a array contains substring occurence indexes
 */
function indexOfAll(str, substring){
  
  let outputArray = [];

  let indexOccurence = str.indexOf(substring, 0);

  while(indexOccurence >= 0) {
    outputArray.push(indexOccurence);

    indexOccurence = str.indexOf(substring, indexOccurence + 1);
  }
  return outputArray;
}

/**
 * find a max difference between each 2 neighbor element
 * 
 * @param {array} arr the array contains n number.
 * 
 * @returns {number} max difference
 */
 function findMaxDifference(arr){
  
  let diff = 0;
  for(let i = 1; i < arr.length; i++){
    if( arr[i]-arr[i-1] > diff)
      diff = arr[i]-arr[i-1];
  }
  return diff;
}