import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import ThumbUpAltOutlinedIcon from '@material-ui/icons/ThumbUpAltOutlined';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import {increaseViews} from '../api/ascii-art-generator-api';
const useStyles = makeStyles((theme) => ({
  title:{
    background: 'linear-gradient(160deg, #2ECC83 30%, #4BFC22 80%)',
    color: 'white',
    borderTopLeftRadius: 0,
    borderTopRightRadius: 16,
    borderBottomRightRadius: 0,
    borderBottomLeftRadius: 0,
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
    fontSize: theme.spacing(2),
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    alignItems: 'baseline',
    justifyContent: 'space-between',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    
  },
  iconDiv:{
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'row',
    alignItems: 'center',
    '&:hover': {
      background: 'rgba(50, 50, 50, 0.08)',
    },
    '& > #user-avator': {
      height:'1.5rem',
      width:'1.5rem',
      margin:'2px',
    },
    '& > *': {
      margin:'2px',
    },
  },
  iconBar: {
    marginTop: 0,
    borderTopLeftRadius: 0,
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
    borderBottomLeftRadius: 16,
  },
  asciiContent: {
    height: theme.spacing(50),
    borderTopLeftRadius: 0,
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
    borderBottomLeftRadius: 0,
  },
  cardDiv:{
    margin: theme.spacing(1),
    
    transition: '0.1s',
    '&:hover': {
      transform: 'scale(1.05)',
      boxShadow: `10px 20px 16px 0px rgba(101, 128, 95, 0.35)`,//#DEDEDE
    },
  }
}));


export default function AsciiCard(props) {
  const classes = useStyles();
  const heightScale = props.asciiHeight>(props.contentHeight/20) ? props.contentHeight/(20*props.asciiHeight): 1.0;
  let width = heightScale * props.asciiWidth *8 ;
  width = width > props.contentHeight/2 ?  width : props.contentHeight/2;
  const mainColor = '#2ECC83';
  let clicked = false;

  const handleClickCard = async (e) => {
    if(!clicked){
      console.log(props.postId);
      increaseViews(props.postId);
      clicked = !clicked;
    }
  }
  return (
    <div className={classes.cardDiv} onClick={handleClickCard}>
    <Card className={classes.title} style = {{ 'width':width }}>  
      <Typography variant="h6" noWrap={true} style = {{ 'width':width-10 }}>{props.title} </Typography>
      <div className={classes.iconDiv}>
        <Avatar id='user-avator'> 
        </Avatar>
        <Typography variant="subtitle2" >
          {props.userName} 
        </Typography> 
      </div>
     
    </Card>

    <Card className={classes.asciiContent} style = {{ 'width':width }}>
      <CardContent style = {{ 'height':props.contentHeight , 'marginTop': 0, 'padding':4}}>
        <Typography variant="body2" component={'div'}>
          <pre style = {{  
            'transformOrigin': '0 0',
            'transform': `scale(${heightScale})`}
          }>
          {props.context}
          </pre>
        </Typography>
        
      </CardContent>
      <CardActions>
        
      </CardActions>
    </Card>
    
    <Card className={classes.iconBar} style = {{ 'width':width }}>
      <CardActions>

        

        <div className={classes.iconDiv} onClick={(e)=>props.handlePostLike(e, props.postId, props.cardId)}>
          <ThumbUpAltOutlinedIcon style={{ color: mainColor }}/>
          <Typography variant="body2" style={{ color: mainColor }}>
            {props.like_num} 
          </Typography> 
        </div>
        <div className={classes.iconDiv}>
          <VisibilityOutlinedIcon style={{ color: mainColor}}/>
          <Typography variant="body2" style={{ color: mainColor}}>
            {props.views} 
          </Typography> 
        </div>

        
      </CardActions>
    </Card>
    </div>
  )
}
